@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
:root {
  --primary-color: #007AFF;
  --secondary-color: #FF9500;
  --background-color: #F2F2F7;
  --text-color: #1D1D1F;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Roboto', sans-serif;
  line-height: 1.6;
  color: var(--text-color);
  background-color: var(--background-color);
}

a {
  color: var(--primary-color);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

button {
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.process-grid {
  margin-top: 20px;
}

.process-category {
  margin-bottom: 30px;
}

.process-category h2 {
  border-bottom: 2px solid #007AFF;
  padding-bottom: 10px;
  margin-bottom: 20px;
}

.process-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  grid-gap: 20px;
  gap: 20px;
}

.process-card {
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.process-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0,0,0,0.15);
}

.process-card h3 {
  margin-top: 10px;
  margin-bottom: 10px;
  color: #007AFF;
}

.process-icon {
  font-size: 2.5em;
  margin-bottom: 15px;
}

.process-card p {
  flex-grow: 1;
  margin-bottom: 15px;
}

.pain-point-indicator {
  align-self: flex-start;
  padding: 5px 10px;
  border-radius: 15px;
  font-size: 0.8em;
  font-weight: bold;
}

.pain-point-0 { background-color: #4CAF50; color: white; }
.pain-point-1 { background-color: #FFC107; color: black; }
.pain-point-2 { background-color: #FF9800; color: white; }
.pain-point-3 { background-color: #F44336; color: white; }

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 30px;
  border-radius: 8px;
  max-width: 600px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 1.5em;
  border: none;
  background: none;
  cursor: pointer;
  color: #666;
}

.modal-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.modal-icon {
  font-size: 2.5em;
  margin-right: 15px;
}

.modal-content h2 {
  color: #007AFF;
  margin: 0;
}

.modal-content h3 {
  margin-top: 20px;
  margin-bottom: 10px;
  color: #333;
}

.modal-content ul {
  padding-left: 20px;
}

.modal-content li {
  margin-bottom: 5px;
}

.app-footer {
  margin-top: 2rem;
  padding: 1rem;
  background-color: #f5f5f5;
  text-align: center;
  font-size: 0.9rem;
  color: #666;
}
.pass-key-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
}

.pass-key-content {
  background-color: white;
  padding: 2rem;
  border-radius: 8px;
  text-align: center;
  max-width: 400px;
  width: 100%;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.2);
}

.logo-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.logo-image-left, .logo-image-right {
  max-height: 40px; /* Adjusted to reduce size */
  width: auto;
  margin: 0 0.5rem;
}

.pass-key-content h2 {
  margin-bottom: 1rem;
  font-size: 1.5rem;
  color: #333;
}

.pass-key-content p {
  margin-bottom: 1.5rem;
  font-size: 0.9rem;
  color: #555;
}

.pass-key-content form {
  display: flex;
  flex-direction: column;
}

.pass-key-content input {
  padding: 0.75rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
}

.pass-key-content button {
  padding: 0.75rem;
  background-color: #007AFF;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.pass-key-content button:hover {
  background-color: #005bb5;
}

.loading-spinner-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: transparent; /* Optional, but can add styling */
  animation: fadeIn 0.3s ease-in-out;
}

.spinner {
  width: 60px;
  height: 60px;
}

.spinner-svg {
  animation: rotate 2s linear infinite;
}

.spinner-path {
  stroke-dasharray: 150, 200;
  stroke-dashoffset: 0;
  stroke-linecap: round;
  animation: dash 1.5s ease-in-out infinite;
}

.loading-text {
  margin-top: 20px;
  font-size: 1.2rem;
  color: #555;
  opacity: 0.85;
  text-align: center;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  padding-top: 120px; /* Increased to account for the larger mobile header */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

@media (max-width: 600px) {
  .app-container {
    padding-top: 140px; /* Further increased for mobile */
  }
}

main {
  flex-grow: 1;
}
